export default {
        namespaced:true,
        state:{ //设置的全局可访问对象
            count:0,
            ChangeShowCom:true,
        },
        // 数据加工
        getters:{
            getCount(state){
                return state.count*10;
            },
        },
        mutations:{
            addCount(state,num){
                state.count+=num;
            },
            delCount(state,num){
                state.count-=num;
            },
            show(state){
                state.ChangeShowCom = true;
            },
            hide(state){
                state.ChangeShowCom = false;
            },
        },
        actions:{
            getAddCount(context,num){
                context.commit('addCount',num);
            },
            getDelCount(context,num){
                if(state.count > 0 ){
                    context.commit('delCount',num);
                }else{
                    state.count=0;
                }
            },
            hideCom(context){
                context.commit('hide');
            },
            showCom(context){
                context.commit('show');
            },
    
        },
}