<template>
  <div class="xx">
    <div class="user-card">
      <van-cell
        class="tx"
        v-for="item in solt"
        :key="item.ltq_id"
        @click="
          data({
            id: item.ltq_id,
            name: item.name,
            status: item.status,
            type: item.type,
          })
        "
      >
        <template #icon>
          <img :src="item.image" class="avatar" />
        </template>
        <template #title>
          <span class="username">{{ item.name }}</span>
        </template>
        <template #label>
          <div class="miaoshu">{{ item.title }}</div>
        </template>
        <div>{{ item.time }}</div>
        <div v-if="type=='xx'">
            <div class="tp" v-if="item.number">
              <span class="tz">{{item.number}}</span>
            </div>
        </div>
        <div  v-if="type=='sj'">
          <van-image
            v-if="item.status"
            class="tz"
            :src="require('../../public/images/jy.png')"
            />
        </div>
      </van-cell>
    </div>
  </div>
</template>

<script>
export default {
  props:['solt','type'],
  data() {
    return {
       
    };
  },
  methods: {
    data(value) {
        this.$router.push({
        name:"chat",
        params:{
          id:value.id,
          name:value.name,
          status:value.status,
        }
      });
      }
    },
};
</script>

<style lang="less" scoped>

.user-card {
  .van-cell__title {
    margin-left: 20px;
    text-align: start;
  }
  .tx {
    text-overflow: ellipsis;
    img {
      height: 8vh;
      width: 8vh;
      border-radius: 5px;
      position: relative;
    }
    .van-cell__label {
      width: 50vw;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
    .username {
      font-size: 18px;
      width: 50vw;
      font-weight: bold;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      letter-spacing: 1px;
      position: absolute;
      top: 16px;
    }
    .miaoshu {
      font-size: 14px;
      margin-top: 5vh;
      letter-spacing: 2px;
      width: 50vw;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      font-weight: bolder;
      background-color: #fff;
      color: #ccc;
    }
    .tp{
      background-color: #ccc;
      height: 20px;
      width: 20px;
      float: right;
      margin-top: 10px;
      border-radius: 50%;
      align-content: center;
      line-height: 20px;
    }
    .tz {
      height: 15px;
      width: 15px;
      margin-right: 6px;
      margin-top: 5px;
    }
  }
}
</style>
