<template>
  <div class="main">
    <div class="box">
      <div
        class="card"
        v-for="index in list"
        :key="index.id"
        @click="market(index.id)"
      >
        <img class="ico" :src="index.icon" />
        <div>{{ index.title }}</div>
      </div>
    </div>

    <van-dialog v-model="show" show-cancel-button @confirm="dialogConfirm">
      <div class="zpxx">填写批发代码</div>
      <van-form ref="form">
        <van-field
          v-model="username"
          label="填写批发代码"
          placeholder="请填写批发代码"
          :rules="[{ required: true }]"
        />
      </van-form>
    </van-dialog>
    <van-dialog v-model="nullshow">
      <div class="zpxx">你输入的代码为空</div>
    </van-dialog>
    <van-dialog v-model="errshow">
      <div class="zpxx">你输入的代码不存在</div>
    </van-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      nullshow:false,
      errshow:false,
      username: "",
      list: [
        { id: 1, icon: require("../../../public/bj1.jpg"), title: "买就送" },
        { id: 2, icon: require("../../../public/bj1.jpg"), title: "批发" },
        { id: 3, icon: require("../../../public/bj1.jpg"), title: "线下购物" },
        { id: 4, icon: require("../../../public/bj1.jpg"), title: "直播" },
        { id: 5, icon: require("../../../public/bj1.jpg"), title: "社交" },
        { id: 6, icon: require("../../../public/bj1.jpg"), title: "批发" },
        { id: 7, icon: require("../../../public/bj1.jpg"), title: "零售市场" },
        { id: 8, icon: require("../../../public/bj1.jpg"), title: "批发市场" },
      ],
    };
  },
  methods: {
    market(value) {
      switch (value) {
        case 1:
          break;
        case 2:
          break;
        case 3:
          break;
        case 4:
          break;
        case 5:
          this.$router.push("/socialize");
          break;
        case 6:
          break;
        case 7:
          break;
        case 8:
          if (this.$store.state.Status.status) {
            this.show = true;
          } else {
            this.$router.push("/details");
          }
          break;
        default:
          this.show = false;
          break;
      }
    },
    dialogConfirm() {
      
      this.$store.commit("Status/setCode", this.username);
      if ((this.username.length == 0)) {
        this.nullshow = true
      }else if(this.username.length == 2 && this.username.substring(1) =='A'){
          this.$store.commit("Status/setStatus", false);
          this.$router.push(`/details/?${this.username}`);
      }else{
        this.errshow = true
      }
      
      
      
      
    },
  },
};
</script>

<style lang="less" scoped>
.box {
  display: flex;
  flex-flow: row wrap;
  margin-top: 5px;
  font-size: 15px;
  font-weight: bolder;
  background-color: white;
  .card {
    width: 25%;
  }
  .ico {
    height: 15vw;
    width: 15vw;
    margin: 15px 0 5px 0;
    border-radius: 10px;
  }
}
.zpxx {
  position: relative;
  margin-top: 20px;
  font-weight: bolder;
  letter-spacing: 10px;
  color: red;
}
</style>
