<template>
  <div class="goods">
    <div class="heade">
      <van-icon name="arrow-left" @click="go" />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
      />
    </div>
    <van-tabs v-model="actice" animated swipeable title-active-color="red">
      <van-tab
        v-for="item in goodslist"
        :key="item.id"
        :title="item.title"
        :name="item.name"
      >
        <lazyload />
        {{ item.name }}
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import lazyload from "@/load/lazy_load";

export default {
  components: {
    lazyload,
  },
  props: ["id"],
  data() {
    return {
      value: "",
      actice: this.id,
      goodslist: [
        { id: 1, title: "订单", name: "1" },
        { id: 2, title: "待付款", name: "2" },
        { id: 3, title: "待发货", name: "3" },
        { id: 4, title: "待收货", name: "4" },
        { id: 5, title: "评价", name: "5" },
      ],
    };
  },

  methods: {
    go() {
      this.$router.push("/index/setup");
    },
  },
};
</script>

<style scoped>
.heade {
  display: flex;
  flex-flow: row nowrap;
  /* background-color: #ccc; */
}
.van-icon {
  height: 34px;
  width: 34px;

  border-radius: 10px;
  align-content: center;
  line-height: 34px;
  margin: 10px;
}
.van-search {
  width: 80%;
}
</style>
